import { ItemsTypeArray } from "../interface";
import {
  WhatsAppOutlined,
  UserOutlined,
  MailOutlined,
} from "@ant-design/icons";

export const formValidation: ItemsTypeArray[] = [
  {
    label: "",
    name: "username",
    icon: {
      prefix: <UserOutlined style={{ color: "rgba(0,0,0,.45)" }} />,
    },
    placeholder: "",
    rule: [
      { required: true, message: "!" },
      {
        pattern: /^(?:[\u4e00-\u9fa5]{2,16})$/,
        message: "!",
      },
      { max: 20, message: "7-20!" },
      { min: 2, message: "2!" },
    ],
  },
  {
    label: "",
    name: "phone",
    icon: {
      prefix: <WhatsAppOutlined style={{ color: "rgba(0,0,0,.45)" }} />,
    },
    placeholder: "",
    rule: [
      { required: true, message: "!" },
      {
        pattern: new RegExp(/^(?:(?:\+|00)86)?1[3-9]\d{9}$/),
        message: "!",
      },
      { max: 11, message: "11!" },
      { min: 11, message: "11!" },
    ],
  },
  {
    label: "",
    name: "email",
    icon: {
      prefix: <MailOutlined style={{ color: "rgba(0,0,0,.45)" }} />,
    },
    placeholder: "",
    rule: [
      { required: true, message: "!" },
      {
        pattern:
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
        message: "!",
      },
      { max: 1000, message: "1000!" },
      { min: 3, message: "3!" },
    ],
  },
];
